/**
 * WordPress dependencies
 */
import { createBlock } from '@wordpress/blocks';
import { __unstableCreateElement as createElement } from '@wordpress/rich-text';

const transforms = {
	from: [
		{
			type: 'block',
			blocks: ['core/buttons'],
			transform: (attributes, content) => {
				const newInnerButtons = [];
				content.forEach((coreButton) => {
					const buttonAttrs = coreButton?.attributes || {};
					const { url, rel, text, className, style = {} } = buttonAttrs;
					newInnerButtons.push(
						createBlock('arkhe-blocks/button', {
							href: url,
							rel,
							content: text,
							radius: style?.border?.radius || undefined,
							style: 'is-style-outline' === className ? 'outline' : 'fill',
							isNewTab: '_blank' === buttonAttrs.linkTarget,
						})
					);
				});
				return createBlock('arkhe-blocks/buttons', attributes, newInnerButtons);
			},
		},
		{
			type: 'block',
			isMultiBlock: true,
			blocks: ['core/paragraph'],
			transform: (paragraphs) => {
				// Creates the buttons block.
				return createBlock(
					'arkhe-blocks/buttons',
					{},
					paragraphs.map((attributes) => {
						const element = createElement(document, attributes.content);
						// Remove any HTML tags.
						const text = element.innerText || '';
						// Get first url.
						const link = element.querySelector('a');
						const href = link?.getAttribute('href');
						// Create singular button in the buttons block.
						return createBlock('arkhe-blocks/button', {
							content: text,
							href,
						});
					})
				);
			},
			// ?
			// isMatch: (paragraphs) => {
			// 	console.log('paragraphs', paragraphs);

			// 	return paragraphs.every((attributes) => {
			// 		const element = createElement(document, attributes.content);
			// 		const text = element.innerText || '';
			// 		const links = element.querySelectorAll('a');
			// 		return text.length <= 30 && links.length <= 1;
			// 	});
			// },
		},
	],
};

export default transforms;
