/**
 * @WordPress dependencies
 */
import { __, _x, sprintf } from '@wordpress/i18n';
import { memo } from '@wordpress/element';
import { Flex, FlexItem, FlexBlock, Icon } from '@wordpress/components';

/**
 * @Internal dependencies
 */
import { ArkbUnitNumber } from '@components';

/**
 * @Others dependencies
 */
// import classnames from 'classnames';

const gapIconX = (
	<svg x='0px' y='0px' viewBox='0 0 56 56'>
		<path d='M53,44H41V12h12V44z M43,42h8V14h-8V42z' />
		<path d='M15,44H3V12h12V44z M5,42h8V14H5V42z' />
		<polygon points='39,28 35,24 35,27 21,27 21,24 17,28 21,32 21,29 35,29 35,32 ' />
	</svg>
);
const gapIconY = (
	<svg x='0px' y='0px' viewBox='0 0 56 56'>
		<path d='M44,3v12H12V3H44z M42,13V5H14v8H42z' />
		<path d='M44,41v12H12V41H44z M42,51v-8H14v8H42z' />
		<polygon points='28,17 24,21 27,21 27,35 24,35 28,39 32,35 29,35 29,21 32,21 ' />
	</svg>
);

/* eslint @wordpress/i18n-translator-comments: 0 */

/**
 * ArkbGapControls
 */
const ArkbGapControls = memo(
	({ name, value, setAttributes, help, successionHelp, defaultGap = {} }) => {
		let helpText = help || '';
		if (successionHelp) {
			helpText = sprintf(__('If empty, %s is taken over', 'arkhe-blocks'), successionHelp);
		}

		const onChange = (changedVal, direction) => {
			const newValue = changedVal || undefined;

			const newGap = { ...value };
			if (undefined === newValue) {
				delete newGap[direction];
			} else {
				newGap[direction] = newValue;
			}

			// 全て空かどうか
			if (!Object.keys(newGap).length) {
				setAttributes({ [name]: undefined });
			} else {
				setAttributes({ [name]: newGap });
			}
		};

		return (
			<div className='arkb-gapControl'>
				<Flex className='arkb-gapControl__row'>
					<FlexItem className='__label'>
						<Icon icon={gapIconX} size='1.5em' />
						{_x('Horizontal', 'margin', 'arkhe-blocks')}
					</FlexItem>
					<FlexBlock>
						<ArkbUnitNumber
							value={value?.x || defaultGap?.x || 'rem'}
							steps={{ em: 0.25, rem: 0.25 }}
							onChange={(val) => {
								onChange(val, 'x');
							}}
							onClear={() => {
								onChange(undefined, 'x');
							}}
						/>
					</FlexBlock>
				</Flex>
				<Flex className='arkb-gapControl__row'>
					<FlexItem className='__label'>
						<Icon icon={gapIconY} size='1.5em' />
						{_x('Vertical', 'margin', 'arkhe-blocks')}
					</FlexItem>
					<FlexBlock>
						<ArkbUnitNumber
							value={value?.y || defaultGap?.y || 'rem'}
							steps={{ em: 0.25, rem: 0.25 }}
							onChange={(val) => {
								onChange(val, 'y');
							}}
							onClear={() => {
								onChange(undefined, 'y');
							}}
						/>
					</FlexBlock>
				</Flex>
				{helpText && <p className='arkb-helpText u-arkb--mt--5'>{helpText}</p>}
			</div>
		);
	}
);

export default ArkbGapControls;
