export default function getFlexLayoutProps(layout, defaultLayout = {}) {
	let {
		orientation = defaultLayout?.orientation || 'horizontal',
		justifyContent = defaultLayout?.justifyContent || 'left',
		verticalAlignment = defaultLayout?.verticalAlignment || 'center',
		flexWrap = defaultLayout?.flexWrap || 'wrap',
	} = layout;

	// justifyContent を flex用の値へ
	if (justifyContent === 'left') {
		justifyContent = 'flex-start';
	} else if (justifyContent === 'right') {
		justifyContent = 'flex-end';
	}

	let layoutStyles = {};
	if (orientation === 'horizontal') {
		// verticalAlignmentをalign-items用の値へ
		if (verticalAlignment === 'top') {
			verticalAlignment = 'flex-start';
		} else if (verticalAlignment === 'bottom') {
			verticalAlignment = 'flex-end';
		}

		layoutStyles = {
			'justify-content': justifyContent,
			'align-items': verticalAlignment,
			'flex-wrap': flexWrap,
		};
	} else {
		// 縦並びのときはjustifyContent の値を'align-items'にセット。
		layoutStyles = {
			'align-items': justifyContent,
			'flex-wrap': flexWrap,
		};
	}

	return layoutStyles;
}
