/**
 * @WordPress dependencies
 */
import {
	__, //_x
} from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import {
	BlockControls,
	InnerBlocks,
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';
import { PanelBody, BaseControl } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import transforms from './transforms';

import { convertStylesToCssCode, getFlexLayoutProps } from '@helper';
import {
	ArkbMarginControl,
	ArkbGapControls,
	DynamicStyles,
	ArkbJustifyContentToolbarControls,
	ArkbVerticalAlignmentToolbarControl,
	ArkbOrientationControl,
	ArkbFlexWrapControl,
	LsSelectorPreviewTip,
} from '@components';

/**
 * @others dependencies
 */
import classnames from 'classnames';

/**
 * style生成
 */
function getDynamicStyles({
	clientId,
	layout,
	gap,
	//gapTab, gapMB
}) {
	const layoutStyles = getFlexLayoutProps(layout, { justifyContent: 'center' });
	const styles = {
		all: {
			...layoutStyles,
			'--arkb-gap--x': gap?.x || '',
			'--arkb-gap--y': gap?.y || '',
		},
		// sp: {
		// 	'--arkb-gap--x': gapTab?.x || '',
		// 	'--arkb-gap--y': gapTab?.y || '',
		// },
		// mb: {
		// 	'--arkb-gap--x': gapMB?.x || '',
		// 	'--arkb-gap--y': gapMB?.y || '',
		// },
	};

	return convertStylesToCssCode(`#block-${clientId}`, styles);
}

/**
 * ボタンブロック
 */
const blockName = 'ark-block-buttons';
registerBlockType(metadata.name, {
	title: __('Buttons', 'arkhe-blocks'),
	icon: blockIcon,
	edit: ({ attributes, setAttributes, clientId }) => {
		const { layout = {}, gap, templateLock } = attributes;
		const { orientation = 'horizontal' } = layout;

		const isHorizontal = orientation === 'horizontal';

		// ブロックprops
		const blockProps = useBlockProps({
			className: `${blockName} ark-has-guide`,
			'data-orientation': orientation,
		});

		// 左右marginの関係でカラムブロックは一つdivかませる
		const innerBlocksProps = useInnerBlocksProps(
			{ className: `${blockName}__inner` },
			{
				template: [['arkhe-blocks/button']],
				allowedBlocks: ['arkhe-blocks/button'],
				templateLock,
			}
		);

		// layout更新処理
		const onChangeLayout = (newLayout) => setAttributes({ layout: newLayout });

		// CSS生成
		const dynamicStyles = getDynamicStyles({ clientId, layout, gap });

		return (
			<>
				{dynamicStyles && <DynamicStyles style={dynamicStyles} />}
				<BlockControls group='block'>
					<ArkbJustifyContentToolbarControls
						layout={layout}
						onChange={onChangeLayout}
						defaultValue={'center'}
					/>
					{isHorizontal && (
						<ArkbVerticalAlignmentToolbarControl
							layout={layout}
							onChange={onChangeLayout}
							defaultValue={'center'}
						/>
					)}
				</BlockControls>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Layout')} initialOpen={true}>
						<ArkbOrientationControl layout={layout} onChange={onChangeLayout} />
						<ArkbFlexWrapControl layout={layout} onChange={onChangeLayout} />
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Margin between buttons', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ArkbGapControls
								value={gap}
								name='gap'
								setAttributes={setAttributes}
								defaultGap={{ x: '1rem', y: '1rem' }}
							/>
						</BaseControl>
					</PanelBody>
				</InspectorControls>
				{/* edit側だけinnner div を噛ませる */}
				<div {...blockProps}>
					<LsSelectorPreviewTip
						icon={blockIcon}
						anchor={attributes.anchor}
						className={attributes.className}
					/>
					<div {...innerBlocksProps}></div>
				</div>
			</>
		);
	},

	save: ({ attributes }) => {
		const { layout = {} } = attributes;
		const { orientation = 'horizontal' } = layout;

		// ブロックprops
		const blockProps = useBlockProps.save({
			className: classnames(`${blockName}`),
			'data-orientation': orientation,
		});
		return (
			<div {...blockProps}>
				<InnerBlocks.Content />
			</div>
		);
	},
	// deprecated,
	transforms,
});
